<template>
 <div class="exam">
      <form v-for="(item,index) in examData" :key="index">
          <div class="question"> 
             <span >{{index+1}}.  {{item.Que}}</span> 
          </div> 

          <div class="answer-options">
                <div class="answer-items"> 
                   {{item.Ans}} 
                </div>
          </div>
      </form>
   </div>

</template>

<script>
import axios from  'axios';
export default {
  data(){
    return{
      examData:[]
    }
  },
  methods:{
     asyncParse(str){
        return new Promise((resolve)=>{
          resolve(JSON.parse(str))
        })
      },
  },
  async beforeMount(){
     const res = await axios.get(`https://portal.army.idf/sites/hafifon383/_api/web/Lists/getByTitle('pending tests')/Items?$filter=(num eq '${this.$route.params.num}') and (type eq '${this.$route.params.examType}')`)
     this.examData = res.data.value
      const promiseAnswers = await Promise.all(this.examData.map((item)=>{
             return this.asyncParse(item.exam).then((inner)=>{
                  item['exam'] = inner
               return {item}
              })
            }))
          this.examData = this.examData[0].exam
          console.log(this.examData)
  }
}
</script>

<style scoped>
form{
